.typewriter h2 {
  overflow: hidden; /* Ensures the content is not revealed until the animation */
  white-space: nowrap; /* Keeps the content on a single line */
  letter-spacing: .05em; /* Adjust as needed */
  animation:
    typing 2s steps(70, end),
    blink-caret .75s step-end infinite;
}
@media screen and (min-width: 701px) {
  .navbar {
      height:10%;
    }
}
.navbar {
  width: 100%;
  background-color: white;
  transition: top 0.6s;
  z-index: 3;
  position: fixed;
  top: 0;
  cursor: default;
  justify-content: space-around;
  display: flex;
  align-items: center;
  font-family: 'Quicksand', sans-serif;
  font-size: 3vh;

}
.navbar a {
  font-size: 100%;
  cursor: pointer;
  border-radius: 20px;
  padding: 8px;

  border: 3px solid transparent;

}

.navbar a:not(:last-child):hover {
  border-radius: 20px;
  padding: 8px;

  border: 3px solid black;
}

.navbar > .active {
  border-radius: 20px;
  padding: 8px;
    border: 3px solid black;
}

@media screen and (max-width: 700px) {
  .navbar a {display: none;}
  .navbar a.icon {
    display: block;
    position: fixed;
    right: 0;
    top: 0;
    font-family: 'Quicksand', sans-serif;
    font-size: 3em;

  }
  .navbar.responsive {
    display: grid;
    justify-content: flex-end;
    position: fixed;
    padding-top: 25%;
    background-color: white;
  }
.navbar.responsive a.icon {
  position:  fixed;
  right: 0;
  top: 0;
  font-family: 'Quicksand', sans-serif;
  font-size: 3em;

}
.navbar.responsive a {
  float: none;
  display: block;
  text-align: right;
}
.navbar.responsive a:not(:last-child):hover {
  border-radius: 20px;
  padding: 8px;

  border: 3px solid black;
}

}

.navbar .icon {
  display: none;
}

.navbar--appear {
  top: -60px;
}

